import { ApiProperty } from '@nestjs/swagger';
import { Transform } from 'class-transformer';
import {
  IsDateString,
  IsNotEmpty,
  IsOptional,
  IsUrl,
  MaxLength,
} from 'class-validator';

import { ICreatePersonnelRequest } from '../../../application/interface';

export class CreatePersonnelRequest implements ICreatePersonnelRequest {
  @ApiProperty({ title: 'Name' })
  @IsNotEmpty()
  @MaxLength(256)
  @Transform(({ value }) => value?.toString().trim())
  name: string;
  @ApiProperty({ title: 'Phone number' })
  @IsNotEmpty()
  @MaxLength(21)
  @Transform(({ value }) => value?.toString().trim())
  phone_number: string;
  @ApiProperty({
    title: 'Date of birth',
    format: 'yyyy-MM-dd',
    example: '2025-01-01',
  })
  @IsDateString({ strict: true })
  date_of_birth: string;
  @ApiProperty({
    title: 'Image url',
    required: false,
    example: 'example.com/123.png',
  })
  @IsOptional()
  @IsUrl()
  image_url?: string;
}
